<template>
	<view class="team-container">
		<!-- È°µÈù¢Â§¥ÈÉ® -->
		<view class="header">
			<view class="title-section">
				<text class="page-title">ÂÖ®ÈÉ®Âõ¢Èòü</text>
				<text class="page-subtitle">ÊµèËßàÊâÄÊúâÂèØÂä†ÂÖ•ÁöÑÂõ¢Èòü</text>
			</view>
			<view class="header-actions">
				<button class="browse-btn" @tap="browseTeams">
					<text class="browse-icon">üîç</text>
				</button>
				<button class="add-btn" @tap="showCreateModal">
					<text class="add-icon">+</text>
				</button>
			</view>
		</view>
		
		<!-- Áæ§ÁªÑÂàóË°® -->
		<view class="group-list" v-if="groups.length > 0">
			<view 
				class="group-item card" 
				v-for="group in groups" 
				:key="group.id"
				@tap="viewGroup(group)"
			>
				<view class="group-header">
					<view class="group-avatar">
						{{ group.name.charAt(0) }}
					</view>
					<view class="group-info">
						<text class="group-name">{{ group.name }}</text>
						<view class="group-meta">
							<text class="member-count">üë• {{ group.member_count }} ÊàêÂëò</text>
							<text class="role-badge" :class="getUserRole(group)">
								<text class="role-icon" v-if="getUserRole(group) === 'admin'">üëë</text>
								{{ getUserRole(group) === 'admin' ? 'Ë¥üË¥£‰∫∫' : getUserRole(group) === 'member' ? 'ÊàêÂëò' : 'Êú™Âä†ÂÖ•' }}
							</text>
						</view>
					</view>
					<view class="group-actions" v-if="getUserRole(group) === 'admin'">
						<text class="action-btn">‚öôÔ∏è</text>
					</view>
				</view>
				
				<view class="group-description" v-if="group.description">
					{{ group.description }}
				</view>
				
				<view class="group-footer">
					<text class="join-date">
						ÂàõÂª∫Êó∂Èó¥: {{ formatDate(group.created_at, 'YYYY-MM-DD') }}
					</text>
					<button 
						class="btn btn-outline small" 
						@tap.stop="joinOrViewGroup(group)"
						:class="{
							'btn-primary': getUserRole(group) === 'none',
							'btn-secondary': getUserRole(group) === 'member' || getUserRole(group) === 'admin'
						}"
					>
						{{ getGroupActionButtonText(group) }}
					</button>
				</view>
			</view>
		</view>
		
		<!-- Á©∫Áä∂ÊÄÅ -->
		<view class="empty-state" v-else-if="!loading">
			<text class="empty-icon">üë•</text>
			<text class="empty-title">ÊöÇÊó†Âõ¢Èòü</text>
			<text class="empty-subtitle">ËøòÊ≤°ÊúâÂàõÂª∫‰ªª‰ΩïÂõ¢ÈòüÔºåÂàõÂª∫‰∏Ä‰∏™Âõ¢ÈòüÂºÄÂßãÁªÑÁªáÊ¥ªÂä®ÂêßÔºÅ</text>
			<button class="btn btn-primary" @tap="showCreateModal">ÂàõÂª∫Á¨¨‰∏Ä‰∏™Âõ¢Èòü</button>
		</view>
		
		<!-- Âä†ËΩΩÁä∂ÊÄÅ -->
		<view class="loading-state" v-if="loading">
			<text>Âä†ËΩΩ‰∏≠...</text>
		</view>
		
		<!-- ÂàõÂª∫Âõ¢ÈòüÂºπÁ™ó -->
		<view class="modal-overlay" v-if="showModal" @tap="hideModal">
			<view class="modal-content" @tap.stop>
				<view class="modal-header">
					<text class="modal-title">ÂàõÂª∫Âõ¢Èòü</text>
					<text class="modal-close" @tap="hideModal">√ó</text>
				</view>

				<view class="modal-body">
					<view class="form-item">
						<text class="label">Âõ¢ÈòüÂêçÁß∞ <text class="required">*</text></text>
						<input
							class="input"
							type="text"
							placeholder="ËæìÂÖ•Âõ¢ÈòüÂêçÁß∞Ôºà2-50‰∏™Â≠óÁ¨¶Ôºâ"
							v-model="groupForm.name"
							:maxlength="50"
						/>
					</view>

					<view class="form-item">
						<text class="label">Âõ¢ÈòüÁ±ªÂûã</text>
						<picker 
							:range="teamTypes" 
							:range-key="'label'"
							:value="teamTypeIndex"
							@change="onTeamTypeChange"
							class="picker"
						>
							<view class="picker-display">
								{{ getSelectedTeamTypeName() }}
								<text class="picker-arrow">‚ñº</text>
							</view>
						</picker>
					</view>

					<view class="form-item">
						<text class="label">Âõ¢ÈòüÊèèËø∞</text>
						<textarea
							class="textarea"
							placeholder="ÊèèËø∞‰∏Ä‰∏ãËøô‰∏™Âõ¢ÈòüÁöÑÁî®ÈÄîÔºàÂèØÈÄâÔºåÊúÄÂ§ö500Â≠óÁ¨¶Ôºâ"
							v-model="groupForm.description"
							:maxlength="500"
						/>
					</view>

					<view class="form-item">
						<text class="label">Âõ¢ÈòüÂ§¥ÂÉè</text>
						<input
							class="input"
							type="text"
							placeholder="ËæìÂÖ•Â§¥ÂÉèURLÔºàÂèØÈÄâÔºâ"
							v-model="groupForm.avatar_url"
						/>
						<text class="form-hint">ÂèØ‰ª•ËæìÂÖ•ÂõæÁâáÈìæÊé•‰Ωú‰∏∫Âõ¢ÈòüÂ§¥ÂÉè</text>
					</view>
				</view>
				
				<view class="modal-footer">
					<button class="btn btn-secondary" @tap="hideModal">ÂèñÊ∂à</button>
					<button class="btn btn-primary" @tap="createGroup" :disabled="saving">
						{{ saving ? 'ÂàõÂª∫‰∏≠...' : 'ÂàõÂª∫' }}
					</button>
				</view>
			</view>
		</view>
		
		<!-- Âõ¢ÈòüËØ¶ÊÉÖÂºπÁ™ó -->
		<view class="modal-overlay" v-if="showDetailModal" @tap="hideDetailModal">
			<view class="modal-content" @tap.stop>
				<view class="modal-header">
					<text class="modal-title">{{ selectedGroup?.name }}</text>
					<text class="modal-close" @tap="hideDetailModal">√ó</text>
				</view>

				<view class="modal-body" v-if="selectedGroup">
					<view class="detail-section">
						<text class="detail-label">Âõ¢ÈòüÊèèËø∞</text>
						<text class="detail-value">{{ selectedGroup.description || 'ÊöÇÊó†ÊèèËø∞' }}</text>
					</view>

					<view class="detail-section">
						<text class="detail-label">ÊàêÂëòÔºà{{ selectedGroup.member_count }}Ôºâ</text>
						<view class="members-preview">
							<view class="member-list-preview">
								<!-- Â¶ÇÊûúÊúâÊàêÂëòÈ¢ÑËßàÊï∞ÊçÆÔºåÊòæÁ§∫ÂÆûÈôÖÊàêÂëò -->
								<template v-if="selectedGroup.membersPreview && selectedGroup.membersPreview.length > 0">
									<text
										v-for="(member, index) in selectedGroup.membersPreview"
										:key="member.id"
										class="member-item"
										:class="{ 'leader': member.is_leader }"
									>
										{{ member.is_leader ? 'üëë' : 'üë§' }} {{ member.nickname }}
									</text>
								</template>
								<!-- Â¶ÇÊûúÊ≤°ÊúâÈ¢ÑËßàÊï∞ÊçÆÔºåÊòæÁ§∫ÈªòËÆ§‰ø°ÊÅØ -->
								<template v-else>
									<text class="member-item leader" v-if="getUserRole(selectedGroup) === 'admin'">
										üëë {{ selectedGroup.leader_name || 'Êàë' }}
									</text>
									<text class="member-item" v-if="selectedGroup.member_count > 1">
										üë§ ÂÖ∂‰ªñÊàêÂëò
									</text>
								</template>

								<!-- Â¶ÇÊûúÊàêÂëòË∂ÖËøá4‰∏™ÔºåÊòæÁ§∫ÁúÅÁï•Âè∑ -->
								<text v-if="selectedGroup.member_count > 4" class="member-more">...</text>
							</view>
							<text class="view-all-members" @tap="viewTeamDetail(selectedGroup)">Êü•ÁúãÂÖ®ÈÉ®ÊàêÂëò</text>
						</view>
					</view>

					<view class="detail-section">
						<text class="detail-label">ÊàëÁöÑËßíËâ≤</text>
						<text class="detail-value">
							<text v-if="getUserRole(selectedGroup) === 'admin'">üëë Ë¥üË¥£‰∫∫</text>
							<text v-else-if="getUserRole(selectedGroup) === 'member'">üë§ ÊàêÂëò</text>
							<text v-else>üö´ Êú™Âä†ÂÖ•</text>
						</text>
					</view>

					<view class="detail-section">
						<text class="detail-label">ÂàõÂª∫Êó∂Èó¥</text>
						<text class="detail-value">{{ formatDate(selectedGroup.created_at, 'YYYYÂπ¥MMÊúàDDÊó•') }}</text>
					</view>
				</view>
				
				<view class="modal-footer">
					<view class="action-buttons">
						<button class="action-btn" @tap="viewTeamDetail(selectedGroup)">
							Âõ¢ÈòüËØ¶ÊÉÖ
						</button>
						<button class="action-btn" @tap="viewActivities(selectedGroup)">
							Âõ¢ÈòüÊ¥ªÂä®
						</button>
						<button
							class="action-btn"
							@tap="viewApplications(selectedGroup)"
							v-if="getUserRole(selectedGroup) === 'admin'"
						>
							Â§ÑÁêÜÁî≥ËØ∑
						</button>
					</view>

					<!-- Ê†πÊçÆÁî®Êà∑ËßíËâ≤ÂíåÁî≥ËØ∑Áä∂ÊÄÅÊòæÁ§∫‰∏çÂêåÁöÑÊåâÈíÆ -->
					<button
						class="btn btn-primary join-btn"
						@tap="applyToJoinGroup(selectedGroup)"
						v-if="canApplyToGroup(selectedGroup)"
					>
						Áî≥ËØ∑Âä†ÂÖ•Âõ¢Èòü
					</button>
					
					<button
						class="btn btn-secondary"
						disabled
						v-if="isGroupApplied(selectedGroup)"
					>
						Â∑≤Áî≥ËØ∑
					</button>
					
					<!-- Âè™ÊúâÊàêÂëòÊâçÊòæÁ§∫ÈÄÄÂá∫ÊåâÈíÆ -->
					<button
						class="btn btn-danger exit-btn"
						@tap="leaveGroup(selectedGroup)"
						v-if="getUserRole(selectedGroup) === 'member'"
					>
						ÈÄÄÂá∫Âõ¢Èòü
					</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import { groupApi } from '../../api/index.js'
	import { formatDate, showSuccess, showError, showConfirm } from '../../utils/index.js'
	
	export default {
		data() {
			return {
				groups: [],
				teamTypes: [
					{ value: 'general', label: 'ÈÄöÁî®Âõ¢Èòü' }
				], // Âõ¢ÈòüÁ±ªÂûãÂàóË°®ÔºåÂàùÂßãÂåñÈªòËÆ§ÂÄº
				loading: false,
				showModal: false,
				showDetailModal: false,
				saving: false,
				selectedGroup: null,
				groupForm: {
					name: '',
					description: '',
					team_type: 'general',
					avatar_url: ''
				},
				// Ê∑ªÂä†Áî®Êà∑Âõ¢ÈòüÂÖ≥Á≥ªÊï∞ÊçÆ
				userTeams: [],
				// Ê∑ªÂä†Áî®Êà∑Áî≥ËØ∑Áä∂ÊÄÅÊï∞ÊçÆ
				userApplications: []
			}
		},
		computed: {
			// ËÆ°ÁÆóÂõ¢ÈòüÁ±ªÂûãÈÄâÊã©Âô®ÁöÑÁ¥¢Âºï
			teamTypeIndex() {
				if (!this.teamTypes || this.teamTypes.length === 0) {
					return 0
				}
				const index = this.teamTypes.findIndex(t => t.value === this.groupForm.team_type)
				return index >= 0 ? index : 0
			}
		},
		onLoad() {
			this.loadInitialData()
		},
		onShow() {
			this.loadInitialData()
		},
		onPullDownRefresh() {
			this.fetchGroups().finally(() => {
				uni.stopPullDownRefresh()
			})
		},
		methods: {
			formatDate,
			
			// Âä†ËΩΩÂàùÂßãÊï∞ÊçÆ
			async loadInitialData() {
				await Promise.all([
					this.fetchGroups(),
					this.loadTeamTypes(),
					this.loadUserTeams(),
					this.loadUserApplications() // Âä†ËΩΩÁî®Êà∑Áî≥ËØ∑ËÆ∞ÂΩï
				])
			},
			
			// Âä†ËΩΩÁî®Êà∑Áî≥ËØ∑ËÆ∞ÂΩï
			async loadUserApplications() {
				try {
					const response = await groupApi.getMyApplications()
					if (response.success) {
						this.userApplications = response.data.applications || []
					}
				} catch (error) {
					console.error('Âä†ËΩΩÁî®Êà∑Áî≥ËØ∑ËÆ∞ÂΩïÂ§±Ë¥•:', error)
					this.userApplications = []
				}
			},
			
			// Âä†ËΩΩÁî®Êà∑Â∑≤Âä†ÂÖ•ÁöÑÂõ¢Èòü
			async loadUserTeams() {
				try {
					const response = await groupApi.getMyTeams()
					if (response.success) {
						const teams = response.data.teams || response.data || []
						this.userTeams = Array.isArray(teams) ? teams : []
					}
				} catch (error) {
					console.error('Âä†ËΩΩÁî®Êà∑Âõ¢ÈòüÂ§±Ë¥•:', error)
					this.userTeams = []
				}
			},

			// Âä†ËΩΩÂõ¢ÈòüÁ±ªÂûã
			async loadTeamTypes() {
				try {
					// ‰ªéÂêéÂè∞APIËé∑ÂèñÂõ¢ÈòüÁ±ªÂûã
					const response = await groupApi.getTeamTypes()
					if (response.success) {
						this.teamTypes = response.data || []
					}
				} catch (error) {
					console.error('Âä†ËΩΩÂõ¢ÈòüÁ±ªÂûãÂ§±Ë¥•:', error)
					// ‰ΩøÁî®ÈªòËÆ§Á±ªÂûã
					this.teamTypes = [
						{ value: 'general', label: 'ÈÄöÁî®Âõ¢Èòü' }
					]
				}
			},
			
			// Ëé∑ÂèñÂõ¢ÈòüÂàóË°®ÔºàÂÖ®ÈÉ®Âõ¢ÈòüÔºâ
			async fetchGroups() {
				this.loading = true
				try {
					// Ë∞ÉÁî®ÂÖ®ÈÉ®Âõ¢ÈòüAPI
					const response = await groupApi.getList()
					if (response.success) {
						const teams = response.data.teams || response.data || []
						this.groups = Array.isArray(teams) ? teams : []
					}
				} catch (error) {
					showError('Ëé∑ÂèñÂõ¢ÈòüÂàóË°®Â§±Ë¥•')
					console.error('Ëé∑ÂèñÂõ¢ÈòüÂàóË°®Â§±Ë¥•:', error)
				} finally {
					this.loading = false
				}
			},
			
			// Ëé∑ÂèñÁî®Êà∑Âú®Âõ¢Èòü‰∏≠ÁöÑËßíËâ≤
			getUserRole(group) {
				const userTeam = this.userTeams.find(t => t.id === group.id)
				if (userTeam) {
					return userTeam.role || 'member'
				}
				return 'none'
			},
			
			// Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶Â∑≤Áî≥ËØ∑Âä†ÂÖ•Âõ¢Èòü
			isGroupApplied(group) {
				return this.userApplications.some(app => 
					app.teamId === group.id && 
					app.status === 'pending'
				)
			},
			
			// Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶ÂèØ‰ª•Áî≥ËØ∑Âä†ÂÖ•Âõ¢Èòü
			canApplyToGroup(group) {
				const userRole = this.getUserRole(group)
				// Êú™Âä†ÂÖ•‰∏îÊú™Áî≥ËØ∑ÁöÑÁî®Êà∑ÊâçÂèØ‰ª•Áî≥ËØ∑
				return userRole === 'none' && !this.isGroupApplied(group)
			},
			
			// Ëé∑ÂèñÂõ¢ÈòüÂç°Áâá‰∏äÁöÑÊåâÈíÆÊñáÊú¨
			getGroupActionButtonText(group) {
				const userRole = this.getUserRole(group)
				if (userRole === 'admin' || userRole === 'member') {
					return 'Êü•ÁúãËØ¶ÊÉÖ'
				}
				
				if (this.isGroupApplied(group)) {
					return 'Â∑≤Áî≥ËØ∑'
				}
				
				return 'Âä†ÂÖ•Âõ¢Èòü'
			},
			
			// Ê†πÊçÆÁî®Êà∑ËßíËâ≤ÂÜ≥ÂÆöÁÇπÂáªË°å‰∏∫
			joinOrViewGroup(group) {
				const userRole = this.getUserRole(group)
				if (userRole === 'none' && !this.isGroupApplied(group)) {
					// Êú™Âä†ÂÖ•‰∏îÊú™Áî≥ËØ∑ÔºåÁõ¥Êé•Áî≥ËØ∑Âä†ÂÖ•Âõ¢Èòü
					this.applyToJoinGroup(group)
				} else {
					// Â∑≤Âä†ÂÖ•ÊàñÂ∑≤Áî≥ËØ∑ÔºåÊü•ÁúãËØ¶ÊÉÖ
					this.viewGroup(group)
				}
			},
			
			// ÊòæÁ§∫ÂàõÂª∫ÂºπÁ™ó
			showCreateModal() {
				this.resetForm()
				this.showModal = true
			},
			
			// ÈöêËóèÂàõÂª∫ÂºπÁ™ó
			hideModal() {
				this.showModal = false
				this.resetForm()
			},
			
			// ÈáçÁΩÆË°®Âçï
			resetForm() {
				this.groupForm = {
					name: '',
					description: '',
					team_type: 'general',
					avatar_url: ''
				}
			},
			
			// ÂàõÂª∫Âõ¢Èòü
			async createGroup() {
				// Ë°®ÂçïÈ™åËØÅ
				if (!this.groupForm.name.trim()) {
					showError('ËØ∑ËæìÂÖ•Âõ¢ÈòüÂêçÁß∞')
					return
				}

				if (this.groupForm.name.trim().length < 2) {
					showError('Âõ¢ÈòüÂêçÁß∞Ëá≥Â∞ë2‰∏™Â≠óÁ¨¶')
					return
				}

				if (this.groupForm.name.trim().length > 50) {
					showError('Âõ¢ÈòüÂêçÁß∞‰∏çËÉΩË∂ÖËøá50‰∏™Â≠óÁ¨¶')
					return
				}

				if (this.groupForm.description && this.groupForm.description.length > 500) {
					showError('Âõ¢ÈòüÊèèËø∞‰∏çËÉΩË∂ÖËøá500‰∏™Â≠óÁ¨¶')
					return
				}

				this.saving = true
				try {
					const data = {
						name: this.groupForm.name.trim(),
						description: this.groupForm.description?.trim() || '',
						team_type: this.groupForm.team_type || 'general',
						avatar_url: this.groupForm.avatar_url?.trim() || null
					}

					const response = await groupApi.create(data)
					if (response.success) {
						showSuccess('Âõ¢ÈòüÂàõÂª∫ÊàêÂäü')
						this.hideModal()
						this.fetchGroups()
						// ÈáçÊñ∞Âä†ËΩΩÁî®Êà∑Âõ¢ÈòüÂàóË°®
						this.loadUserTeams()
					} else {
						throw new Error(response.message || 'ÂàõÂª∫Â§±Ë¥•')
					}
				} catch (error) {
					console.error('ÂàõÂª∫Âõ¢ÈòüÂ§±Ë¥•:', error)
					showError(error.message || 'ÂàõÂª∫Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï')
				} finally {
					this.saving = false
				}
			},
			
			// Êü•ÁúãÂõ¢ÈòüËØ¶ÊÉÖ
			async viewGroup(group) {
				this.selectedGroup = group
				// Ëé∑ÂèñÂõ¢ÈòüÊàêÂëòÈ¢ÑËßà‰ø°ÊÅØ
				await this.loadTeamMembersPreview(group.id)
				this.showDetailModal = true
			},

			// ÈöêËóèËØ¶ÊÉÖÂºπÁ™ó
			hideDetailModal() {
				this.showDetailModal = false
				this.selectedGroup = null
			},

			// Êü•ÁúãÂõ¢ÈòüÊ¥ªÂä®
			viewActivities(group) {
				this.hideDetailModal()
				uni.switchTab({
					url: `/pages/activity/activity?groupId=${group.id}`
				})
			},
			
			// Áî≥ËØ∑Âä†ÂÖ•Âõ¢Èòü
			async applyToJoinGroup(group) {
				try {
					const response = await groupApi.apply(group.id, {
						reason: 'Â∏åÊúõËÉΩÂ§üÂä†ÂÖ•Ëøô‰∏™Âõ¢ÈòüÔºåÂèÇ‰∏éÂõ¢ÈòüÊ¥ªÂä®ÂíåÈ°πÁõÆÔºå‰∏éÂ§ßÂÆ∂‰∏ÄËµ∑Â≠¶‰π†ÂíåÊàêÈïø„ÄÇ'
					})
					if (response.success) {
						showSuccess('Áî≥ËØ∑Â∑≤Êèê‰∫§ÔºåËØ∑Á≠âÂæÖÂÆ°Ê†∏')
						// Êõ¥Êñ∞Êú¨Âú∞Áî≥ËØ∑Áä∂ÊÄÅ
						this.userApplications.push({
							id: response.data.id,
							teamId: group.id,
							status: 'pending',
							applicationTime: response.data.applicationTime
						})
						// Âà∑Êñ∞Âõ¢ÈòüÂàóË°®‰ª•Êõ¥Êñ∞Áä∂ÊÄÅ
						this.loadInitialData()
					} else {
						throw new Error(response.message || 'Áî≥ËØ∑Â§±Ë¥•')
					}
				} catch (error) {
					console.error('Áî≥ËØ∑Âä†ÂÖ•Âõ¢ÈòüÂ§±Ë¥•:', error)
					showError(error.message || 'Áî≥ËØ∑Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï')
				}
			},
			
			// Á¶ªÂºÄÁæ§ÁªÑ
			async leaveGroup(group) {
				const confirmed = await showConfirm(`Á°ÆÂÆöË¶ÅÁ¶ªÂºÄÂõ¢Èòü"${group.name}"ÂêóÔºü`)
				if (!confirmed) return

				try {
					await groupApi.leave(group.id)
					showSuccess('Â∑≤Á¶ªÂºÄÂõ¢Èòü')
					this.hideDetailModal()
					// ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
					this.loadInitialData()
				} catch (error) {
					console.error('Á¶ªÂºÄÂõ¢ÈòüÂ§±Ë¥•:', error)
					showError('Á¶ªÂºÄÂõ¢ÈòüÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï')
				}
			},

			// ÊµèËßàÂõ¢Èòü
			browseTeams() {
				uni.navigateTo({
					url: '/pages/team-browse/team-browse'
				})
			},

			// Êü•ÁúãÁî≥ËØ∑ÁÆ°ÁêÜ
			viewApplications(group) {
				uni.navigateTo({
					url: `/pages/team-applications/team-applications?teamId=${group.id}&teamName=${encodeURIComponent(group.name)}`
				})
			},

			// Êü•ÁúãÂõ¢ÈòüËØ¶ÊÉÖÂíåÊàêÂëòÂàóË°®
			viewTeamDetail(group) {
				this.hideDetailModal()
				uni.navigateTo({
					url: `/pages/team-detail/team-detail?id=${group.id}`
				})
			},

			// Âä†ËΩΩÂõ¢ÈòüÊàêÂëòÈ¢ÑËßàÔºàÂâç4‰∏™ÊàêÂëòÔºâ
			async loadTeamMembersPreview(teamId) {
				try {
					// ÊâÄÊúâÁî®Êà∑ÈÉΩÂèØ‰ª•Ëé∑ÂèñÊàêÂëòÂàóË°®
					const response = await groupApi.getTeamMembers(teamId)
					if (response.success && response.data.members) {
						// ÂèñÂâç4‰∏™ÊàêÂëòÁî®‰∫éÈ¢ÑËßà
						this.selectedGroup.membersPreview = response.data.members.slice(0, 4)
						this.selectedGroup.leader_name = response.data.members.find(m => m.is_leader)?.nickname || 'Ë¥üË¥£‰∫∫'
					}
				} catch (error) {
					console.log('Ëé∑ÂèñÊàêÂëòÈ¢ÑËßàÂ§±Ë¥•:', error)
					// Â¶ÇÊûúËé∑ÂèñÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§ÊòæÁ§∫
				}
			},

			// Âõ¢ÈòüÁ±ªÂûãÈÄâÊã©Áõ∏ÂÖ≥ÊñπÊ≥ï
			onTeamTypeChange(e) {
				const index = e.detail.value
				this.groupForm.team_type = this.teamTypes[index].value
			},

			getSelectedTeamTypeName() {
				if (!this.teamTypes || this.teamTypes.length === 0) {
					return 'ÈÄöÁî®Âõ¢Èòü'
				}
				const type = this.teamTypes.find(t => t.value === this.groupForm.team_type)
				return type ? type.label : 'ÈÄöÁî®Âõ¢Èòü'
			},
		}
	}
</script>

